import * as React from "react";
import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";
import MenuItem from "./MenuItem";

export interface MenuProps extends React.HTMLAttributes<HTMLUListElement>, NamespaceWrapperProps {
  /**
   * Size of the menu
   */
  size?: "large" | "medium" | "small";

  /**
   * Display the menu as a block
   */
  block?: boolean;
}

export default class Menu extends NamespaceWrapper<MenuProps> {
  render() {
    const {namespace, className, block, size, ...props} = this.props;
    const classes = this.classes(
      "Menu",
      (block && "Menu--block"),
      (size && "Menu--" + size),
      className
    );
    return <ul className={classes} {...props} />;
  }
}

export interface MenuHeaderProps extends React.LiHTMLAttributes<HTMLLIElement>, NamespaceWrapperProps {
}

export class MenuHeader extends NamespaceWrapper<MenuHeaderProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <li className={this.classes("Menu__header", className)} {...props} />;
  }
}

export interface MenuDividerProps extends React.LiHTMLAttributes<HTMLLIElement>, NamespaceWrapperProps {
}

export class MenuDivider extends NamespaceWrapper<MenuDividerProps> {
  render() {
    const {namespace, className, ...props} = this.props;
    return <li className={this.classes("Menu__divider " + className)} {...props} />;
  }
}

export { MenuItem };
